.share {
    width: 100%;
    height: 170px;
    border-radius: 5px;
    /* background-color: darkorange; */
    -webkit-box-shadow:0px 1px 0px rgba(0,0,0,0.67);
    box-shadow: 0px 1px 7px 0px rgba(0,0,0,0.67);
}
.shareWrapper {
    padding: 10px;
}
.shareTop {
    display :flex;
    align-items: center;
}
.shareProfileImage {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.shareInput {
    width: 80%;
    font-size: 18px;
   border:none;
}
.shareInput:focus{
    outline: none;
}
.sharehr {
    margin-top: 20px;
}
.shareBottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
 
.shareImage{
    margin-right: 3px;
}
.shareOptions{
    display: flex;
    margin-left: 20px;
    align-items: center;

}
.shareOption {
    display: flex;
    margin-right:35px;
    align-items: center;
    cursor: pointer;
    font-size: 18px;
    justify-content: space-between;
}
.shareOptionText{
    font-size: 16px;
}
.shareButton {
    padding: 8px;
    margin-left: 140px;
    float: left;
    border:none;
    border-radius: 5px;
    background-color: green;
    font-weight: 500;
    color: white;
    font-style:bold;

}